export const STORE = {
  _data:{
    chirpList: [],
    currentNavRoute: ""
  },

  getStoreData: function(){
    return this._data
  },

  setStore: function(storeProp, payload){
    this._data[storeProp] = payload
    this._callBack()
  },

  onStoreChange: function(cb){
    this._callBack = cb
  }

}



